import { PanelData } from '@grafana/data';
import { getDataSourceSrv } from '@grafana/runtime';
import { useEffect, useState } from 'react';

import { ENGINE_DATA_SOURCE } from '../constants';
import { DataSourceApi } from '../types';

/**
 * Get Data Source
 *
 * @async
 */
const getEngineDatasource = async (data: PanelData): Promise<DataSourceApi | null> => {
  const targets = data.request?.targets.filter((target) => target.datasource?.type === ENGINE_DATA_SOURCE);
  if (!targets || !targets.length || !targets[0].datasource) {
    return null;
  }

  /**
   * Data Source
   */
  const datasource: DataSourceApi = await getDataSourceSrv().get(targets[0].datasource);
  if (!datasource || !datasource.api) {
    return null;
  }

  return datasource;
};

/**
 * Use Data Source Api
 * @param data
 */
export const useDatasourceApi = (data: PanelData) => {
  /**
   * Data Source
   */
  const [datasource, setDatasource] = useState<DataSourceApi | null>(null);

  /**
   * Load Data Source
   */
  useEffect(() => {
    const getDatasource = async () => {
      const datasource = await getEngineDatasource(data);

      setDatasource(datasource);
    };

    getDatasource();
  }, [data]);

  return datasource;
};
