import { getBackendSrv } from '@grafana/runtime';
import { lastValueFrom } from 'rxjs';

import { AlertTimeRange, AlertVariableOption, Dashboard, DashboardInfo, UserDetails } from '../types';
import { BaseApi } from './base';

/**
 * Grafana Api
 */
export class Grafana extends BaseApi {
  async getDashboards() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Dashboard[]>({
        url: `${this.api.instanceSettings.url}/grafana/search`,
        method: 'GET',
        params: {
          limit: 1000,
        },
      })
    ).catch(() => {});

    if (!response) {
      return [];
    }

    return response.data.filter((dashboard) => dashboard.type === 'dash-db');
  }

  async getDashboardInfo(id: string) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<DashboardInfo>({
        url: `${this.api.instanceSettings.url}/grafana/dashboards/uid/${id}`,
        method: 'GET',
      })
    ).catch(() => {});

    if (!response) {
      return null;
    }

    return response.data;
  }

  async getVariableOptions(
    dashboardId: string,
    variables: Record<string, string | string[]>,
    timeRange?: AlertTimeRange
  ): Promise<Record<string, AlertVariableOption | AlertVariableOption[]> | null> {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Record<string, AlertVariableOption | AlertVariableOption[]>>({
        url: `${this.api.instanceSettings.url}/grafana/variables/load`,
        method: 'POST',
        data: {
          dashboardId,
          variables,
          timeRange,
        },
      })
    );

    return response.data;
  }

  async getOrgUsers(): Promise<UserDetails[]> {
    const response = await lastValueFrom(
      getBackendSrv().fetch<UserDetails[]>({
        url: `${this.api.instanceSettings.url}/grafana/org/users`,
        method: 'GET',
      })
    ).catch(() => {});

    if (!response) {
      return [];
    }

    return response.data;
  }
}
