import { DataFrame, DataSourceInstanceSettings, FieldType, TimeRange, toDataFrame } from '@grafana/data';
import { FetchError, getBackendSrv } from '@grafana/runtime';
import { t } from 'i18next';
import { lastValueFrom } from 'rxjs';

import { ApiName, DataSourceOptions, ErrorResponse, Query, QueryRequestType } from '../types';
import { getApiMessage, notifyError } from '../utils';
import { Alerts } from './alerts';
import { Grafana } from './grafana';
import { License } from './license';
import { Webhooks } from './webhooks';

/**
 * API
 */
export class Api {
  /**
   * Constructor
   */
  constructor(public instanceSettings: DataSourceInstanceSettings<DataSourceOptions>) {}

  /**
   * Alerts Api
   */
  alerts = new Alerts(this);

  /**
   * Grafana Api
   */
  grafana = new Grafana(this);

  /**
   * Webhooks Api
   */
  webhooks = new Webhooks(this);

  /**
   * License Api
   */
  license = new License(this);

  /**
   * Ping
   */
  async ping() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<{
        message: string;
      }>({
        method: 'GET',
        url: `${this.instanceSettings.url}/ping`,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.PING, e.data.code)]);
    });

    /**
     * Check Response
     */
    if (!response || !response.data) {
      // notifyError([t('messages.error'), getApiMessage(ApiName.PING)]);
      console.error('error');
      return null;
    }

    /**
     * Pong received
     */
    return response.data;
  }

  /**
   * Get data
   */
  async getData(query: Query, range: TimeRange): Promise<DataFrame> {
    if (query.requestType === QueryRequestType.ALERT_HISTORY) {
      return this.alerts.getHistoryEventsFrame(query);
    }

    return toDataFrame({
      refId: query.refId,
      fields: [
        { name: 'Time', values: [range.from, range.to], type: FieldType.time },
        { name: 'Value', values: [1, 2], type: FieldType.number },
      ],
    });
  }
}
