import { FieldConfigSource, PanelModel, TypedVariableModel } from '@grafana/data';

import { AlertTimeRange } from './alerts';

/**
 * Dashboard
 */
export interface Dashboard {
  /**
   * ID
   *
   * @type {number}
   */
  id: number;

  /**
   * Uid
   *
   * @type {string}
   */
  uid: string;

  /**
   * Title
   *
   * @type {string}
   */
  title: string;

  /**
   * Uri
   *
   * @type {string}
   */
  uri: string;

  /**
   * Type
   *
   * @type {'dash-db' | string}
   */
  type: 'dash-db' | string;
}

/**
 * Panel
 */
export interface Panel extends Omit<PanelModel, 'fieldConfig'> {
  /**
   * Repeat
   *
   * @type {string}
   */
  repeat?: string;

  /**
   * Field Config
   *
   * @type {FieldConfigSource}
   */
  fieldConfig?: FieldConfigSource;
}

/**
 * Dashboard Info
 */
export interface DashboardInfo extends Dashboard {
  /**
   * Panels
   *
   * @type {Panel[]}
   */
  panels: Panel[];

  /**
   * Time Range
   *
   * @type {AlertTimeRange}
   */
  time?: AlertTimeRange;

  /**
   * Templating
   */
  templating: {
    /**
     * List
     *
     * @type {TypedVariableModel[]}
     */
    list: TypedVariableModel[];
  };
}

/**
 * User details
 */
export interface UserDetails {
  /**
   * User Id
   *
   * @type {number}
   */
  userId: number;

  /**
   * Login
   *
   * @type {string}
   */
  login: string;

  /**
   * Email
   *
   * @type {string}
   */
  email: string;
}

/**
 * Variable Type
 */
export enum VariableType {
  QUERY = 'query',
  CUSTOM = 'custom',
  INTERVAL = 'interval',
  DATASOURCE = 'datasource',
  CONSTANT = 'constant',
  TEXTBOX = 'textbox',
}
