import { DataQueryRequest, DataQueryResponse, DataSourceApi, DataSourceInstanceSettings } from '@grafana/data';
import { t } from 'i18next';

import { Api } from '../api';
import { DataSourceOptions, DataSourceTestStatus, Query } from '../types';

/**
 * Datasource
 */
export class DataSource extends DataSourceApi<Query, DataSourceOptions> {
  /**
   * Api
   *
   * @type {Api} api
   */
  api: Api;

  /**
   * Constructor
   */
  constructor(instanceSettings: DataSourceInstanceSettings<DataSourceOptions>) {
    super(instanceSettings);
    this.api = new Api(instanceSettings);
  }

  /**
   * Query
   */
  async query(options: DataQueryRequest<Query>): Promise<DataQueryResponse> {
    const { range } = options;

    /**
     * Process targets
     */
    const data = await Promise.all(options.targets.map((target) => this.api.getData(target, range)));

    /**
     * Return data
     */
    return { data };
  }

  /**
   * Health Check
   */
  async testDatasource() {
    /**
     * Is Status Ok
     */
    const pong = await this.api.ping();

    if (!pong) {
      return {
        status: DataSourceTestStatus.ERROR,
        message: t('api.testDatasource.connectionFailed'),
      };
    }

    /**
     * Return
     */
    return {
      status: DataSourceTestStatus.SUCCESS,
      message: t('api.testDatasource.connected'),
    };
  }
}
