import { FetchError, getBackendSrv } from '@grafana/runtime';
import { t } from 'i18next';
import { lastValueFrom } from 'rxjs';

import { EditWebhookData, ErrorResponse, Webhook } from '../types';
import { ApiName, getApiMessage, notifyError } from '../utils';
import { BaseApi } from './base';

/**
 * Webhooks Api
 */
export class Webhooks extends BaseApi {
  /**
   * Get Webhooks
   */
  async getAll() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Webhook[]>({
        url: `${this.api.instanceSettings.url}/webhooks`,
        method: 'GET',
      })
    ).catch((e) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_WEBHOOKS)]);
      throw e;
    });

    if (!response) {
      return [];
    }

    return response.data;
  }

  /**
   * Create webhook
   */
  async create<T extends EditWebhookData>({ id, ...webhook }: Exact<T, EditWebhookData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Webhook>({
        url: `${this.api.instanceSettings.url}/webhooks`,
        method: 'POST',
        data: webhook,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.CREATE_WEBHOOK, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Update Webhook
   */
  async update<T extends EditWebhookData>(id: string, { id: notUsedId, ...webhook }: Exact<T, EditWebhookData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Webhook>({
        url: `${this.api.instanceSettings.url}/webhooks/${id}`,
        method: 'PUT',
        data: webhook,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.UPDATE_WEBHOOK, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Delete webhook
   * @param id
   */
  async delete(id: string) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Webhook>({
        url: `${this.api.instanceSettings.url}/webhooks/${id}`,
        method: 'DELETE',
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.DELETE_WEBHOOK, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Get Default Webhook
   */
  async getDefault() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Webhook>({
        url: `${this.api.instanceSettings.url}/webhooks/default`,
        method: 'GET',
      })
    ).catch(() => {});

    if (!response) {
      return null;
    }

    return response.data;
  }

  /**
   * Test Webhook
   */
  async test<T extends EditWebhookData>({ id, ...webhook }: Exact<T, EditWebhookData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<{ ok: boolean; message: string }>({
        url: `${this.api.instanceSettings.url}/webhooks/test`,
        method: 'POST',
        data: webhook,
      })
    ).catch((e: FetchError<{ ok: boolean; message: string }>) => {
      return e;
    });

    return response.data;
  }
}
