/**
 * Webhook Type
 */
export enum WebhookType {
  REQUEST = 'request',

  /**
   * Custom for testing purpose
   */
  CUSTOM = 'custom',
}

/**
 * Webhook Method
 */
export enum WebhookMethod {
  POST = 'POST',
}

/**
 * Webhook Base
 */
interface WebhookBase {
  /**
   * ID
   *
   * @type {string}
   */
  id: string;

  /**
   * Title
   *
   * @type {string}
   */
  title: string;

  /**
   * Type
   *
   * @type {WebhookType}
   */
  type: WebhookType;

  /**
   * Is Default
   *
   * @type {boolean}
   */
  isDefault: boolean;

  /**
   * Use For
   *
   * @type {string[]}
   */
  useFor?: string[];

  /**
   * Use For By Default
   *
   * @type {string[]}
   */
  useForByDefault?: string[];
}

/**
 * Webhook Request Options
 */
export interface WebhookRequestOptions {
  /**
   * URL
   *
   * @type {string}
   */
  url: string;
}

/**
 * Webhook
 */
export type Webhook = WebhookBase &
  (
    | {
        type: WebhookType.REQUEST;
        options: WebhookRequestOptions;
      }
    | { type: WebhookType.CUSTOM; options: object }
  );

/**
 * Edit Webhook Data
 */
export interface EditWebhookData extends Omit<Webhook, 'useFor'> {}
