/**
 * Evaluation Type
 */
export enum EvaluationType {
  ANOMALY = 'anomaly',
  PATTERN = 'pattern',
  THRESHOLDS = 'thresholds',
}

/**
 * Alert Variable Value
 */
export type AlertVariableValue = string | string[];

/**
 * Alert Variable
 */
export interface AlertVariable {
  /**
   * Name
   *
   * @type {string}
   */
  name: string;

  /**
   * Multi
   *
   * @type {boolean}
   */
  multi: boolean;

  /**
   * Allow custom value
   *
   * @type {boolean}
   */
  allowCustomValue?: boolean;

  /**
   * Order
   *
   * @type {number}
   */
  order: number;

  /**
   * Label
   *
   * @type {string}
   */
  label?: string;

  /**
   * Value
   *
   * @type {string[] | string}
   */
  value: AlertVariableValue;
}

/**
 * Alert Variable Option
 */
export interface AlertVariableOption {
  /**
   * Label
   *
   * @type {string}
   */
  label: string;

  /**
   * Value
   *
   * @type {string}
   */
  value: string;
}

/**
 * Alert Condition Base
 */
interface AlertConditionBase {
  /**
   * Id
   *
   * @type {string}
   */
  id: string;

  /**
   * Repeat variable
   *
   * @type {string | null}
   */
  repeat: string | null;
}

/**
 * Alert Evaluation
 */
export type AlertEvaluation = AlertEvaluationPattern | AlertEvaluationThresholds | { type: EvaluationType.ANOMALY };

/**
 * Alert Condition Type
 */
export enum AlertConditionType {
  GLOBAL = 'global',
  ITEM = 'item',
}

/**
 * Alert Condition Global
 */
export interface AlertConditionGlobal extends AlertConditionBase {
  /**
   * Type
   *
   * @type {AlertConditionType}
   */
  type: AlertConditionType.GLOBAL;

  /**
   * Global variables
   *
   * @type {Record<string, AlertVariable>}
   */
  variables?: Record<string, AlertVariable>;

  /**
   * Evaluation Type
   *
   * @type {EvaluationType}
   */
  evaluationType: EvaluationType.THRESHOLDS | EvaluationType.ANOMALY | EvaluationType.PATTERN;

  /**
   * Evaluation
   *
   * @type {AlertEvaluation}
   */
  evaluation: AlertEvaluation;
}

/**
 * Alert Condition Item
 */
export interface AlertConditionItem extends AlertConditionBase {
  /**
   * Type
   *
   * @type {AlertConditionType}
   */
  type: AlertConditionType.ITEM;

  /**
   * Variables Override
   *
   * @type {Record<string, AlertVariableValue>}
   */
  variables?: Record<string, AlertVariableValue>;

  /**
   * Evaluation
   *
   * @type {AlertEvaluation}
   */
  evaluation?: AlertEvaluation;
}

/**
 * Alert Condition
 */
export type AlertCondition = AlertConditionGlobal | AlertConditionItem;

/**
 * Alert Evaluation Pattern
 */
export interface AlertEvaluationPattern {
  /**
   * Type
   *
   * @type {EvaluationType}
   */
  type: EvaluationType.PATTERN;

  /**
   * Field
   *
   * @type {string}
   */
  field: string;

  /**
   * Pattern
   *
   * @type {string}
   */
  pattern: string;
}

/**
 * Alert Evaluation Thresholds
 */
export interface AlertEvaluationThresholds {
  /**
   * Type
   *
   * @type {EvaluationType}
   */
  type: EvaluationType.THRESHOLDS;

  /**
   * Allowable Colors
   *
   * @type {string[]}
   */
  colors: string[];
}
