import { DataSourcePluginOptionsEditorProps } from '@grafana/data';
import { FieldSet, InlineField, InlineFieldRow, Input } from '@grafana/ui';
import React, { ChangeEvent, useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { DataSourceOptions, SecureJsonData } from '../../types';

/**
 * Editor Properties
 */
interface Props extends DataSourcePluginOptionsEditorProps<DataSourceOptions> {}

/**
 * Config Editor
 */
export const ConfigEditor: React.FC<Props> = ({ options, onOptionsChange }) => {
  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * URL Change
   */
  const onUrlChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      onOptionsChange({
        ...options,
        jsonData: {
          ...options.jsonData,
          url: event.target.value,
        },
      });
    },
    [onOptionsChange, options]
  );

  /**
   * Token Change
   * Secure fields only sent to the backend
   */
  const onTokenChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      onOptionsChange({
        ...options,
        secureJsonData: {
          token: event.target.value,
        },
      });
    },
    [onOptionsChange, options]
  );

  const { jsonData, secureJsonFields } = options;
  const secureJsonData = (options.secureJsonData || {}) as SecureJsonData;

  return (
    <FieldSet data-testid={TEST_IDS.configEditor.root}>
      <InlineFieldRow>
        <InlineField label={t('engineDatasource.configEditor.urlLabel')} labelWidth={14}>
          <Input
            type="text"
            value={jsonData.url}
            width={40}
            onChange={onUrlChange}
            data-testid={TEST_IDS.configEditor.fieldUrl}
          />
        </InlineField>
      </InlineFieldRow>

      <InlineFieldRow>
        <InlineField label={t('engineDatasource.configEditor.tokenLabel')} labelWidth={14} grow>
          <Input
            type="password"
            placeholder={secureJsonFields?.token ? t('engineDatasource.configEditor.configured') : ''}
            value={secureJsonData.token ?? ''}
            onChange={onTokenChange}
            data-testid={TEST_IDS.configEditor.fieldAccessToken}
          />
        </InlineField>
      </InlineFieldRow>
    </FieldSet>
  );
};
