import { QueryEditorProps, SelectableValue } from '@grafana/data';
import { InlineField, InlineFieldRow, Select } from '@grafana/ui';
import { defaults } from 'lodash';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { DEFAULT_QUERY, REQUEST_TYPE_OPTIONS } from '../../constants';
import { DataSource } from '../../datasource';
import { DataSourceOptions, Query, QueryRequestType } from '../../types';

/**
 * Editor Properties
 */
type Props = QueryEditorProps<DataSource, Query, DataSourceOptions>;

/**
 * Query Editor
 */
export const QueryEditor: React.FC<Props> = ({ query: rawQuery, onRunQuery, onChange }) => {
  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Request Type change
   */
  const onRequestTypeChange = useCallback(
    async (item: SelectableValue<QueryRequestType>) => {
      onChange({ ...rawQuery, requestType: item.value });
      onRunQuery();
    },
    [onChange, onRunQuery, rawQuery]
  );

  /**
   * Query with defaults
   */
  const query = defaults(rawQuery, DEFAULT_QUERY);

  /**
   * Render
   */
  return (
    <>
      <InlineFieldRow>
        <InlineField grow label={t('engineDatasource.queryEditor.requestTypeLabel')} labelWidth={12}>
          <Select options={REQUEST_TYPE_OPTIONS} value={query.requestType} onChange={onRequestTypeChange} />
        </InlineField>
      </InlineFieldRow>
    </>
  );
};
