import React from 'react';

/**
 * App Settings
 */
export interface AppSettings {}

/**
 * Language
 */
export const enum Language {
  DE = 'de',
  EN = 'en',
  ES = 'es',
  FR = 'fr',
  ZH = 'zh',
}

/**
 * Supported Language
 */
export type SupportedLanguage = Language.EN | Language.ES | Language.FR | Language.DE | Language.ZH;

/**
 * SVG
 */
export interface SvgProps extends React.HTMLAttributes<SVGElement> {
  /**
   * Size
   *
   * @type {number}
   */
  size: number;

  /**
   * Fill primary color
   *
   * @type {string}
   */
  fill?: string;

  /**
   * Fill secondary color
   *
   * @type {string}
   */
  fill2?: string;

  /**
   * Title
   *
   * @type {string}
   */
  title?: string;

  /**
   * Class Name
   *
   * @type {string}
   */
  className?: string;
}
