import { FieldType, toDataFrame } from '@grafana/data';
import { FetchError, getBackendSrv } from '@grafana/runtime';
import { t } from 'i18next';
import { lastValueFrom } from 'rxjs';

import {
  Alert,
  AlertHistory,
  AlertStatus,
  AlertWithInfo,
  DashboardInfo,
  EditAlertData,
  ErrorResponse,
  Query,
} from '../types';
import { ApiName, getApiMessage, notifyError } from '../utils';
import { BaseApi } from './base';

/**
 * Alerts Api
 */
export class Alerts extends BaseApi {
  /**
   * Is Alert Target Exists
   * @param alert
   * @param dashboardInfo
   */
  static isAlertTargetExists(alert: Alert, dashboardInfo: DashboardInfo) {
    const panel = dashboardInfo.panels.find((panel) => panel.id === alert.panelId);

    return !!panel;
  }

  /**
   * Get Alerts
   */
  async getAll(): Promise<AlertWithInfo[]> {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Alert[]>({
        url: `${this.api.instanceSettings.url}/alerts`,
        method: 'GET',
      })
    ).catch((e) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_ALERTS)]);
      throw e;
    });

    if (!response) {
      return [];
    }

    /**
     * Get Unique Dashboard Ids
     */
    const dashboardIds = new Set<string>();

    response.data.forEach(({ dashboardId }) => dashboardIds.add(dashboardId));

    /**
     * Get Dashboards Info Map
     */
    const dashboardsInfo = await Promise.all(
      Array.from(dashboardIds).map((dashboardId) => this.api.grafana.getDashboardInfo(dashboardId).catch(() => null))
    );

    /**
     * Dashboards Info Map
     */
    const dashboardsInfoMap = dashboardsInfo.reduce((acc, dashboardInfo) => {
      if (dashboardInfo) {
        acc.set(dashboardInfo.uid, dashboardInfo);
      }
      return acc;
    }, new Map<string, DashboardInfo>());

    /**
     * Get alert with addition info
     */
    return response.data.map((alert) => {
      const dashboardInfo = dashboardsInfoMap.get(alert.dashboardId);

      return {
        ...alert,
        dashboardTitle: dashboardInfo?.title || null,
        panelTitle: dashboardInfo?.panels.find((panel) => panel.id === alert.panelId)?.title || null,
        isTargetExists: dashboardInfo ? Alerts.isAlertTargetExists(alert, dashboardInfo) : false,
      };
    });
  }

  /**
   * Create alert
   */
  async create<T extends EditAlertData>({ id, ...alert }: Exact<T, EditAlertData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Alert>({
        url: `${this.api.instanceSettings.url}/alerts`,
        method: 'POST',
        data: alert,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.CREATE_ALERT, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Update alert
   */
  async update<T extends EditAlertData>(id: string, { id: notUsedId, ...alert }: Exact<T, EditAlertData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Alert>({
        url: `${this.api.instanceSettings.url}/alerts/${id}`,
        method: 'PUT',
        data: alert,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.UPDATE_ALERT, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Update alert status
   */
  async updateStatus(id: string, status: AlertStatus) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Alert>({
        url: `${this.api.instanceSettings.url}/alerts/${id}/status`,
        method: 'PUT',
        data: {
          status,
        },
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.UPDATE_ALERT_STATUS, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Delete alert
   * @param id
   */
  async delete(id: string) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Alert>({
        url: `${this.api.instanceSettings.url}/alerts/${id}`,
        method: 'DELETE',
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.DELETE_ALERT, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Get History Events
   * @param alertIds
   */
  private async getHistoryEvents(alertIds?: string[]): Promise<AlertHistory[]> {
    const response = await lastValueFrom(
      getBackendSrv().fetch<AlertHistory[]>({
        url: `${this.api.instanceSettings.url}/alerts/history`,
        method: 'GET',
        params: {
          ids: alertIds,
        },
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_HISTORY_EVENTS, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data || [];
  }

  /**
   * Get Last History Event
   * @param alertId
   */
  async getLastHistoryEvent(alertId: string): Promise<AlertHistory | null> {
    const response = await lastValueFrom(
      getBackendSrv().fetch<AlertHistory | null>({
        url: `${this.api.instanceSettings.url}/alerts/${alertId}/history/last`,
        method: 'GET',
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_HISTORY_EVENT, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data || null;
  }

  /**
   * Get History Events Frame
   */
  async getHistoryEventsFrame(query: Query) {
    const historyEvents = await this.getHistoryEvents(query.alertIds);

    /**
     * Values Map
     */
    const valuesMap = Object.keys(historyEvents[0]).reduce(
      (acc, key) => ({
        ...acc,
        [key as keyof AlertHistory]: [],
      }),
      {} as {
        id: Array<AlertHistory['id']>;
        alertId: Array<AlertHistory['alertId']>;
        alertTitle: Array<AlertHistory['alertTitle']>;
        duration: Array<AlertHistory['duration']>;
        event: Array<AlertHistory['event']>;
        prevState: Array<AlertHistory['prevState']>;
        newState: Array<AlertHistory['newState']>;
        startDate: Array<AlertHistory['startDate']>;
        endDate: Array<AlertHistory['endDate']>;
      }
    );

    historyEvents.forEach((history) => {
      valuesMap.id.push(history.id);
      valuesMap.alertId.push(history.alertId);
      valuesMap.alertTitle.push(history.alertTitle);
      valuesMap.duration.push(history.duration);
      valuesMap.event.push(history.event);
      valuesMap.prevState.push(history.prevState);
      valuesMap.newState.push(history.newState);
      valuesMap.startDate.push(history.startDate);
      valuesMap.endDate.push(history.endDate);
    });

    return toDataFrame({
      refId: query.refId,
      fields: [
        {
          name: 'id',
          type: FieldType.string,
          values: valuesMap.id,
        },
        {
          name: 'alertId',
          type: FieldType.string,
          values: valuesMap.alertId,
        },
        {
          name: 'alertTitle',
          type: FieldType.string,
          values: valuesMap.alertTitle,
        },
        {
          name: 'duration',
          type: FieldType.number,
          values: valuesMap.duration,
        },
        {
          name: 'event',
          type: FieldType.string,
          values: valuesMap.event,
        },
        {
          name: 'prevState',
          type: FieldType.string,
          values: valuesMap.prevState,
        },
        {
          name: 'newState',
          type: FieldType.string,
          values: valuesMap.newState,
        },
        {
          name: 'startDate',
          type: FieldType.time,
          values: valuesMap.startDate,
        },
        {
          name: 'endDate',
          type: FieldType.time,
          values: valuesMap.endDate,
        },
      ],
    });
  }
}
