import { AlertCondition } from './alert-conditions';

/**
 * Alert Status
 */
export enum AlertStatus {
  ACTIVE = 'active',
  PAUSED = 'paused',
}

/**
 * Alert Time Range Source
 */
export enum AlertTimeRangeSource {
  CONFIGURED = 'configured',
  DASHBOARD = 'dashboard',
}

/**
 * Alert Annotation Mode
 */
export enum AlertAnnotationMode {
  PANEL = 'panel',
  DASHBOARD = 'dashboard',
  DISABLED = 'disabled',
}

/**
 * Alert State
 */
export enum AlertState {
  SCHEDULED = 'scheduled',
  OK = 'ok',
  ALERTING = 'alerting',
  ERROR = 'error',
}

/**
 * Alert Time Range
 */
export interface AlertTimeRange {
  /**
   * From
   *
   * @type {number}
   */
  from: number;

  /**
   * To
   *
   * @type {number}
   */
  to: number;
}

/**
 * Base Alert
 */
interface BaseAlert {
  /**
   * Id
   *
   * @type {string}
   */
  id: string;

  /**
   * Title
   *
   * @type {string}
   */
  title: string;

  /**
   * Dashboard ID
   *
   * @type {string}
   */
  dashboardId: string;

  /**
   * Panel ID
   *
   * @type {number}
   */
  panelId: number;

  /**
   * Schedule
   *
   * @type {string}
   */
  schedule: string;

  /**
   * Webhook Ids
   *
   * @type {string[]}
   */
  webhookIds: string[];

  /**
   * Annotations
   *
   * @type {AlertAnnotationMode}
   */
  annotations: AlertAnnotationMode;

  /**
   * Status
   *
   * @type {AlertStatus}
   */
  status: AlertStatus;

  /**
   * Time Range Source
   *
   * @type {AlertTimeRangeSource}
   */
  timeRangeSource: AlertTimeRangeSource;

  /**
   * Time Range
   *
   * @type {AlertTimeRange}
   */
  timeRange?: AlertTimeRange;

  /**
   * State
   *
   * @type {AlertState}
   */
  state: AlertState;

  /**
   * Last Triggered
   *
   * @type {string | null}
   */
  lastTriggered: string | null;

  /**
   * Conditions
   *
   * @type {AlertCondition[]}
   */
  conditions: AlertCondition[];

  /**
   * State Start Date
   *
   * @type {string | null}
   */
  stateStartDate: string | null;
}

/**
 * Alert
 */
export type Alert = BaseAlert;

/**
 * Edit Alert Data
 */
export interface EditAlertData
  extends Omit<Alert, 'dashboardId' | 'panelId' | 'state' | 'lastTriggered' | 'stateStartDate'> {
  dashboardId: Nullable<Alert['dashboardId']>;
  panelId: Nullable<Alert['panelId']>;
}

/**
 * Alert With Info
 */
export type AlertWithInfo = Alert & {
  /**
   * Dashboard Title
   *
   * @type {string | null}
   */
  dashboardTitle: string | null;

  /**
   * Panel Title
   *
   * @type {string | null}
   */
  panelTitle: string | null;

  /**
   * Is Target Exists
   *
   * @type {boolean}
   */
  isTargetExists: boolean;
};

/**
 * Alert History
 */
export interface AlertHistory {
  /**
   * Id
   *
   * @type {string}
   */
  id: string;

  /**
   * Alert Id
   *
   * @type {string}
   */
  alertId: string;

  /**
   * Alert Title
   *
   * @type {string}
   */
  alertTitle: string;

  /**
   * Start Date
   *
   * @type {Date}
   */
  startDate: Date;

  /**
   * End Date
   *
   * @type {Date}
   */
  endDate: Date | null;

  /**
   * Event
   *
   * @type {string}
   */
  event: string;

  /**
   * Prev State
   *
   * @type {AlertState}
   */
  prevState: AlertState | 'none';

  /**
   * New State
   *
   * @type {AlertState}
   */
  newState: AlertState;

  /**
   * Duration ms
   *
   * @type {number}
   */
  duration: number;
}

/**
 * Threshold
 */
export interface Threshold {
  /**
   * Color
   *
   * @type {string}
   */
  color: string;

  /**
   * Value
   *
   * @type {number | null}
   */
  value: number | null;
}
