import { ApiName, notifyError } from '@bi/volkovlabs-engine-datasource';
import { LicenseInfo } from '@bi/volkovlabs-engine-datasource/types/license';
import { getApiMessage } from '@bi/volkovlabs-engine-datasource/utils';
import { getBackendSrv } from '@grafana/runtime';
import { t } from 'i18next';
import { lastValueFrom } from 'rxjs';

import { BaseApi } from './base';

/**
 * License Api
 */
export class License extends BaseApi {
  /**
   * Get license info
   */
  async get() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<LicenseInfo>({
        url: `${this.api.instanceSettings.url}/license`,
        method: 'GET',
      })
    ).catch((e) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_LICENSE_INFO)]);
      throw e;
    });

    if (!response) {
      return null;
    }

    return response.data;
  }
}
