import { t } from 'i18next';

import { ApiName, ErrorCode } from '../types';

/**
 * Get Api Message
 * @param name
 * @param code
 */
export const getApiMessage = (name: ApiName, code?: ErrorCode): string => {
  if (code === ErrorCode.LICENSE_VERIFICATION_FAILED) {
    return t('api.licenseVerificationFailed');
  }

  if (code === ErrorCode.LIMIT_REACHED) {
    return t('api.limitReached');
  }

  return t(`api.requestFailed`, { name: t(`api.names.${name}`) });
};

/**
 * Export constants to use in one import
 */
export { ApiName };
